import styled from 'styled-components';
import { Col } from 'react-bootstrap';
import '../../../styles/animations/fade-in-scale.scss';

const createAnimationDelay = (num: number) => {
  const css = [];
  for (let i = 1; i <= num; i++) {
    css.push(`&:nth-of-type(${i}) {
      animation-delay: ${i * 100}ms;
    }`);
  }
  return css.join();
};

export const StyledCol = styled(Col)`
  opacity: 0;
  scale: 0.8;
  -webkit-animation: fade-in-scale forwards 1s ease;
  animation: fade-in-scale forwards 1s ease;
  transition: all 0.2s ease;
  position: relative;

  ${createAnimationDelay(18)}

  img, a {
    max-width: 100%;
    transition: all 0.5s ease;
  }

  a {
    display: none;
  }

  &:hover {
    img,
    a {
      scale: 1.1;
    }

    a {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 100%;
      display: flex;
      text-align: center;
      background: rgba(0, 0, 0, 0.85);
      color: white;
      text-decoration: none;
      justify-content: center;
      padding: 0.5rem 1rem;
      align-items: center;
    }
  }
`;
